@import url(https://fonts.googleapis.com/css2?family=MuseoModerno:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Barlow:800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/portfolio/static/media/diagonals.6a94fd4c.jpeg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.title, h1 {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    color: #2a9d8f;
    font-size: 30px;
    font-family: 'Raleway', sans-serif;
}

.nav-bar {
    display: flex;
    align-items: center;
    color: #caf0f8;
    font-weight: bold;
    display: flex;
    justify-content: flex-end;
    background-color: black;
    opacity: 0.6;
    height: 60px;
    font-family: 'MuseoModerno', cursive;
    margin-bottom: 30px;
}

.nav-bar-item {
    list-style: none;
    padding-right: 60px;
    text-decoration: none;
    color: #caf0f8;
}


footer {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    color: white;
    font-size: small;
    opacity: 0.7;
    height: 2.5rem;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
}
#main-projects {
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    margin-left: 100px;
    margin-right: 100px;
}

.link {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    /* text-decoration: none; */
    text-transform: uppercase;
    color: #ff0054;
    width: 100%;
}

.project-card {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    align-content: flex-start;
    font-size: large;
    color: #e76f51;
    margin-top: 10px;
}

.project-descr {
    align-content: flex-start;
    text-transform: none;
    text-align: justify;
    text-justify: inter-word;
    line-height: 1.7;
    margin: 50px;
    height: 25%;
}

.tech-stack-title {
    margin-right: 50px;
    margin-left: 50px;
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    align-content: flex-start;
    width: 100%;
    color: #00f5d4;


}

.tech-stack {
    margin-right: 50px;
    margin-left: 50px;
    line-height: 1.7;
    color: #00f5d4;
}

.image1 {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    content: url(/portfolio/static/media/travel-needs.c82c4222.png);
    height: 400px;
    margin-top: 40px;
}

.image2 {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    content: url(/portfolio/static/media/weather.357eb06d.png);
    height: 400px;
    margin-top: 40px;
}

.image3 {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    content: url(/portfolio/static/media/baldur.3deb04d0.png);
    height: 400px;
    margin-top: 40px;
}
.main-about {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    color: #f4a261;
    margin-left: 300px;
    margin-right: 300px;
    text-align: justify;
    text-justify: inter-word;
    line-height: 1.8;
    flex-direction: column;
    margin-top: 50px;
    font-size: large;
}

#cc-grad {
    margin: 0px;
}
